import React, { Component } from 'react';
import './Topbar.less';

class TopBars extends Component {
    render() {
        return (
            <header className="headerBar">
                <i className="music_logo">QQ</i>
                <a className="link_download">APP</a>
            </header>
        );
    }
}

export default TopBars;


